.locationCount{
    width: 100%;
    height: 35px;
    text-align: left;
    padding-left: 10px;
    color:#888;
    line-height: 35px;
    overflow: hidden;
}
.locationCount h1{
    margin: 0;
    white-space: nowrap;
}
.weatherLocationCont{
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-right: 10px;
    min-width: 350px;
    box-sizing: border-box;
    
    padding: 15px;
    font-family: 'Roboto','sans-serif';
    box-shadow: 10px 10px 40px -12px rgba(0,0,0,0.75 );
    cursor : pointer; 
}